<template>
    <Button @click="handleRemove" :size="size" :type="type"><slot></slot></Button>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
    props: {
        size: {
            type: String,
            default: 'default'
        },
        type: {
            type: String,
            default: 'default'
        },
        to: {
            type: String,
            default: ''
        }
    },
    activated () {
        /**
         * route
         */
        this.setRoute()
    },
    created () {
        /**
         * removetag
         * tagthis.$routetagremovetagroute
         */
        this.setRoute()
    },
    methods: {
        ...mapMutations([
            'removeTag'
        ]),
        setRoute () {
            let { name, query, params } = this.$route
            this.route = { name, query, params }
        },
        handleRemove (to) {
            this.removeTag({ route: this.route, to: this.to || to })
        }
    }
}
</script>
