/**
 * meta: {
 *  title: { String } params: { rename: '' }
 *  active: { String } 
 *  notCache: (false) truenamename
 *  beforeCloseName: (-) tab'@/router/before-close.js'
 *  fill: false truepadding
 * }
 */

export default [
    {
        path: '/login',
        name: 'login',
        component: () => import('@/views/login/login.vue')
    },
    {
        path: '/',
        redirect: '/home',
        component: () => import('_c/main/main.vue'),
        children: [
            {
                path: '/home',
                name: 'home',
                component: () => import('@/views/Home.vue'),
                meta: {
                    fill: true
                }
            },
            {
                path: '/auth/user',
                name: 'auth_user',
                component: () => import('@/views/auth/user/list.vue')
            },
            {
                path: '/auth/user/add',
                name: 'auth_user_add',
                meta: {
                    title: '',
                    active: 'auth_user'
                    // notCache: true
                },
                component: () => import('@/views/auth/user/add.vue')
            },
            {
                path: '/auth/user/edit',
                name: 'auth_user_edit',
                meta: {
                    title: '',
                    active: 'auth_user'
                    // notCache: true
                },
                component: () => import('@/views/auth/user/edit.vue')
            },
            {
                path: '/auth/role',
                name: 'auth_role',
                component: () => import('@/views/auth/role.vue'),
                meta: {
                    beforeCloseName: 'before_close_normal'
                }
            },
            {
                path: '/iframes',
                name: 'iframes',
                component: () => import('@/views/Iframes.vue')
            },
            // ...process.env.NODE_ENV === 'development' ? require('./demo').default : [],
            ...require('./demo').default
        ]
    },
    {
        path: '/loading',
        name: 'loading',
        component: () => import('_c/error/loading.vue')
    },
    {
        path: '/401',
        name: 'error_401',
        component: () => import('_c/error/401.vue')
    },
    {
        path: '/500',
        name: 'error_500',
        component: () => import('_c/error/500.vue')
    },
    {
        path: '*',
        name: 'error_404',
        component: () => import('_c/error/404.vue')
    }
]
