import directive from './directives'

const importDirective = Vue => {
    /**
     *  v-draggable="options"
     * options = {
     *  trigger: /CSS/,
     *  body:    /CSS/,
     *  recover: //
     * }
     */
    Object.keys(directive).forEach(key => {
        Vue.directive(key, directive[key])
    })
}

export default importDirective
